@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

.flx,
.header-cont-cards,
.header,
.ftr,
.main-works-row,
.main {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

body {
  font-family: NSARegular, "Roboto", sans-serif;
  overflow-x: hidden;
  background-color: #F8F2E4;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0px 15px;
}

button {
  border: none;
  background: transparent;
}

/*Modals*/

.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9;
  background-image: linear-gradient(75deg, rgba(210, 0, 17, 0.7) -5%, rgba(0, 49, 155, 0.7) 60%, rgb(234, 163, 0) 90%);
}

/*Our works*/

.works {
  padding: 5rem 0;
}

.works img {
  margin-bottom: 1rem;
}

a:focus {
  outline: none;
}

.bigImg {
  width: 500px;
  background-position: center center;
  background-size: cover;
  box-shadow: 10px -20px;
}

header {
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center center;
  background-attachment: fixed;
}

.contr {
  max-width: 1200px;
  margin: 10px auto;
}

#present {
  background: linear-gradient(to top, transparent, rgba(2, 35, 106, 0.7)) no-repeat bottom;
  padding: 2rem 0;
}

.animated {
  animation: fadeInRight 1s ease-in-out;
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(300px);
  }

  to {
    opacity: 1;
  }
}

.menu-toggle {
  width: 65px;
  height: auto;
  float: left;
  margin-left: 30px;
  margin-top: 10px;
  overflow: hidden;
  padding: 4px;
}

.txtMyu {
  font-size: 22px;
  color: #FFFFFF;
  margin-left: 120px;
  overflow: hidden;
  margin-top: 10px;
  font-family: "Marck Script", cursive;
  text-shadow: 0px 0px 0 rgb(0, 0, 0), 0px 1px 0 rgb(0, 0, 0), 0px 2px 1px rgba(0, 0, 0, 0.01), 0px 2px 2px rgba(0, 0, 0, 0.5), 0px 0px 2px rgba(0, 0, 0, 0.2);
}

.menu-toggle span {
  display: block;
  height: 4px;
  margin: 6px;
  width: 95%;
  background-color: rgb(7, 4, 4);
  border-radius: 20%;
  box-shadow: 0 1px 1px #FFFFFF;
}

.nav {
  width: 220px;
  height: 100%;
  overflow: hidden;
  transition: all 1s linear;
  position: absolute;
  top: 36px;
  left: -240px;
  bottom: 0;
  z-index: 999;
  background-color: rgb(0, 0, 0);
}

.mobile {
  transform: translate(100%);
  list-style: none;
  text-decoration: none;
  position: fixed;
  line-height: 2;
  top: 56px;
  transition: all 1s linear;
}

.nav ul {
  margin-top: 30px;
}

.nav ul li {
  margin-left: 30%;
}

.nav ul li a {
  width: 153px;
  text-decoration: none;
  margin: 5px;
  color: white;
  font-size: 20px;
  list-style: none;
  text-decoration: none;
  display: flex;
  letter-spacing: 1px;
  text-shadow: 1px 1px 1px hsla(0deg, 0%, 100%, 0.6);
}

nav {
  width: 250px;
  height: 50px;
  backdrop-filter: blur(0.85rem);
  box-shadow: 0 0 10px;
  background-color: rgba(51, 45, 65, 0.062745098);
  position: fixed;
  top: 0;
  left: 0;
  text-align: left;
  z-index: 9999;
  overflow: hidden;
}

.main {
  width: 100%;
  backdrop-filter: blur(0.65rem);
  box-shadow: 0 0 10px;
  background-color: rgba(51, 45, 65, 0.062745098);
  margin-top: 30px;
}

.main-pers {
  position: relative;
}

.main-pers-cont {
  display: flex;
  flex-direction: column;
}

.main-pers-cont-inf {
  position: relative;
  width: 100%;
  height: auto;
}

.main-pers-cont-inf p {
  text-align: center;
  margin-left: 12px;
}

.main-pers-cont-inf p span {
  display: block;
  font-size: 15px;
  text-align: left;
  line-height: 2;
  font-weight: 500;
  font-weight: 700;
  color: #0F002A;
}

.main-pers-cont-inf p span:first-of-type {
  font-size: 25px;
  margin-bottom: 10px;
  font-weight: 500;
}

.main-pers-cont-inf p span:first-of-type:hover {
  text-decoration: underline;
  cursor: pointer;
}

.main-pers-cont-img {
  width: 100%;
  min-height: 50vh;
  position: relative;
}

.main-pers-cont-img img {
  margin-top: 20px;
  position: absolute;
  object-fit: cover;
  top: 5px;
  left: 30px;
  width: 200px;
  height: 200px;
  border-radius: 20px;
}

.main-pers-cont-img img:hover {
  cursor: pointer;
  box-shadow: 0px 0px 30px -4px rgba(73, 71, 71, 0.6);
}

.main-pers-cont-img::before {
  content: " ";
  background-size: contain;
  background-repeat: no-repeat;
  width: 240px;
  height: 280px;
  position: absolute;
  top: 5px;
  left: 20px;
  border-right: dashed 10px #5E4A3F;
  border-bottom: dashed 3px #5E4A3F;
  border-radius: 20%;
}

.main-works {
  position: relative;
}

.main-works h2 {
  text-align: center;
  margin: 20px auto;
  font-size: 17px;
  margin-top: 5px;
  color: black;
  font-weight: 600;
  letter-spacing: 1px;
  text-shadow: 0px 0px 0 rgb(0, 0, 0), 0px 1px 0 rgb(0, 0, 0), 0px 2px 1px rgba(236, 23, 23, 0.2), 0px 2px 2px rgba(42, 10, 117, 0.2), 0px 0px 2px rgba(237, 237, 17, 0.2);
}

.main-works-row div {
  position: relative;
  margin: 0 3px;
}

.main-works-row div:hover {
  transition: all 200ms ease-in;
  transform: scale(1.1);
  z-index: 22;
}

.main-works-row div a {
  text-decoration: none;
}

.main-works-row div img {
  width: 100%;
  height: 250px;
  background-repeat: no-repeat;
  background-position: center center;
  object-fit: cover;
  background-attachment: fixed;
  box-shadow: 0 0 10px;
}

.ftr {
  height: 90px;
  padding: 20px;
  background-color: #000000;
  overflow: hidden;
  padding-bottom: 20px;
  border-top: 16px solid #4b4a4a;
  justify-content: center;
  align-items: center;
  font-size: 17px;
  color: #4b4a4a;
}

.footer-name a {
  color: white;
  text-decoration: none;
}

.header {
  margin-top: 0;
  width: 100%;
  min-height: 550px;
  background-image: linear-gradient(75deg, rgba(210, 0, 17, 0.7) -5%, rgba(0, 49, 155, 0.7) 60%, rgba(234, 163, 0, 0.6) 90%);
  justify-content: center;
  align-items: center;
}

.header-title {
  position: relative;
  width: 100%;
  font-weight: 700;
  letter-spacing: 1.4px;
  font-family: "Uchen", serif;
  text-align: center;
  color: wheat;
  font-size: 45px;
  text-shadow: 0px 0px 0 rgb(0, 0, 0), 0px 1px 0 rgb(0, 0, 0), 0px 2px 1px rgba(24, 23, 23, 0.2), 0px 2px 2px rgba(59, 59, 60, 0.2), 0px 0px 2px rgba(80, 80, 78, 0.2);
}

.header-cont {
  position: relative;
  width: 100%;
}

.header-cont-cards {
  align-items: baseline;
}

.header-cont-cards div {
  position: relative;
  width: 100%;
  margin: 6px 0;
}

.header-cont-cards_item {
  width: 100%;
  background-color: #FFFFFF;
  box-shadow: 0 0 10px;
}

.header-cont-cards_item img {
  width: 100%;
  height: 250px;
  background-position: center center;
  background-size: cover;
}

.header-cont-cards_item span {
  font-size: 17px;
  display: block;
  margin-top: 5px;
  text-align: left;
  color: black;
  font-weight: 600;
  padding: 10px;
  letter-spacing: 1px;
  text-shadow: 0px 0px 0 rgb(0, 0, 0), 0px 1px 0 rgb(0, 0, 0), 0px 2px 1px rgba(236, 23, 23, 0.2), 0px 2px 2px rgba(42, 10, 117, 0.2), 0px 0px 2px rgba(237, 237, 17, 0.2);
}

.present-slider {
  width: 100%;
  position: relative;
  height: auto;
}

.present-slider-btn {
  margin-top: 10px;
  z-index: 2;
  display: inline-flex;
}

.present-slider-btn img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  height: auto;
  background-position: center center;
  background-size: cover;
  box-shadow: 0 0 10px;
  margin-right: 30px;
}

.present-slider-btn:hover {
  cursor: pointer;
}

.present-slider-item {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.present-slider-item-block {
  position: relative;
  width: 100%;
}

.present-slider-item-block img {
  max-width: 260px;
  min-height: 250px;
  background-position: center center;
  background-size: cover;
  box-shadow: 0 0 10px;
}

.present-slider-item-block2 {
  text-align: left;
  position: relative;
  width: 100%;
}

.present-slider-item-heading {
  margin-top: 4rem;
  font-size: 22px;
  font-family: "Circe Extra Bold", sans-serif;
  line-height: 1.1;
}

.present-slider-item-text {
  margin: 3rem 0;
  font-size: 20px;
}

