@font-face {
  font-family: NSABold;
  src: url("../fonts/NotoSansArmenian-Bold.ttf");
}

@font-face {
  font-family: NSARegular;
  src: url("../fonts/NotoSansArmenian-Regular.ttf");
}

@font-face {
  font-family: NSAMedium;
  src: url("../fonts/NotoSansArmenian-Medium.ttf");
}

@font-face {
  font-family: NSALight;
  src: url("../fonts/NotoSansArmenian-Light.ttf");
}

@font-face {
  font-family: NSAThin;
  src: url("../fonts/NotoSansArmenian-Thin.ttf");
}

@font-face {
  font-family: Vrdznagir Regular;
  src: url("../fonts/Vrdznagir.otf") format("opentype");
}

@media (min-width: 992px) {
  .main-pers {
    flex: 0 0 33.33333333%;
    width: 33.33333333%;
  }

  .main-works {
    flex: 0 0 66.66666667%;
    width: 66.66666667%;
  }

  .main-works-row div {
    flex: 0 0 24%;
    width: 24%;
  }

  .header-title {
    flex: 0 0 50%;
    max-width: 50%;
  }

  .header-cont {
    flex: 0 0 50%;
    max-width: 50%;
  }

  .header-cont-cards div {
    flex: 0 0 50%;
    max-width: 50%;
  }

  .present-slider-item-block {
    flex: 0 0 30%;
    max-width: 30%;
  }

  .present-slider-item-block2 {
    flex: 0 0 70%;
    max-width: 70%;
  }
}

@media screen and (max-width: 1680px) {
  header {
    background-size: cover;
  }

  header {
    background-size: cover;
  }
}

@media (max-width: 992px) {
  .header-cont {
    flex: 0 0 100%;
    max-width: 100%;
  }

  .header-cont-cards div {
    flex: 0 0 50%;
    max-width: 50%;
  }
}

@media (max-width: 500px) {
  .header-title {
    flex: 0 0 100%;
    max-width: 100%;
    font-size: 35px;
    margin-top: 80px;
  }

  .header-cont-cards div {
    flex: 0 0 100%;
    max-width: 100%;
    padding: 10px;
  }

  .header-cont-cards_item {
    width: 90%;
  }

  .header-cont-cards_item img {
    object-fit: cover;
  }

  .present {
    padding: 8px;
  }
}